import Axios from "axios";
import React from "react";
import { toast } from "react-toastify";
import "../../App.css";
export default function FormContact() {
  const [values, setValues] = React.useState({
    name: "",
    subject: "",
    email: "",
    message: "",
  });

  const [Loading, setLoading] = React.useState(false);

  const notify = (type) => {
    switch (type) {
      case "success":
        toast.info(
          "Se ha enviado el mensaje correctamente, pronto nos comunicaremos",
          {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
          }
        );
        break;

      case "error":
        toast.error(
          "No se ha podido enviar el mensaje por favor intentade nuevo",
          {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
          }
        );

        break;

      case "empty inputs":
        toast.error("Por favor diligenciar todos los campos", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
        });

        break;
    }
  };

  function validate() {
    setLoading(true);
    if (
      values.name === "" ||
      values.subject === "" ||
      values.email === "" ||
      values.message === ""
    ) {
      setLoading(false);
      notify("empty inputs");
    } else {
      Axios.post("https://apicontacthefest.herokuapp.com/send", values)
        .then(function () {
          setLoading(false);
          notify("success");
        })
        .catch(function () {
          setLoading(false);
          notify("error");
        });
    }
  }
  function handleChange(evt) {
    const value = evt.target.value;
    setValues({
      ...values,
      [evt.target.name]: value,
    });
  }
  const sendEmail = () => validate();
  return (
    <>
      <form class="bg-white  shadow-md border border-gray-400 rounded w-auto mx-2 px-3 pt-6 pb-8 mb-4">
        <div class="mb-6">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="name">
            Nombre
          </label>
          <input
            class="shadow appearance-none border placeholder-gray-700 border-gray-500 rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            name="name"
            type="text"
            value={values.name}
            onChange={handleChange}
          />
        </div>

        <div class="mb-6">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="name">
            Asunto
          </label>
          <input
            class="shadow appearance-none border placeholder-gray-700 border-gray-500 rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            name="subject"
            type="text"
            value={values.subject}
            onChange={handleChange}
          />
        </div>
        <div class="mb-6">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="email">
            Correo electronico de contacto
          </label>
          <input
            class="shadow appearance-none border border-gray-500 placeholder-gray-700  rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            name="email"
            type="text"
            value={values.email}
            onChange={handleChange}
          />
        </div>

        <div class="mb-6">
          <label
            class="block text-gray-700 text-sm font-bold mb-2"
            for="message"
          >
            Mensaje
          </label>
          <textarea
            name="message"
            class="shadow appearance-none border border-gray-500 placeholder-gray-700 rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            rows="12"
            value={values.message}
            onChange={handleChange}
          ></textarea>
        </div>
        <div>
          {Loading ? (
            <button
              type="button"
              className=" flex items-center justify-center  btn-form
                 focus:outline-none focus:shadow-outline"
              disabled
            >
              <div className="loader mx-4  w-10 h-10 "></div>
              Enviando Mensaje , Por favor espere
            </button>
          ) : (
            <button
              class="btn-form hover:bg-blue-700 focus:outline-none focus:shadow-outline"
              type="button"
              onClick={sendEmail}
            >
              Enviar Mensaje
            </button>
          )}
        </div>
      </form>
    </>
  );
}
