import React, { useEffect, useState } from "react";
import app from "../../config/firebase";
import GalleryWorks from "./GalleryWorks";

let storageRef;
export default function CardWork({ project }) {
  const [Images, setImages] = useState([]);

  useEffect(() => {
    (async () => {
      await getImages(project);
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const getImages = async (project) => {
    let tempImages = [];

    storageRef = app.storage().ref(project.urlImages);
    console.log("folder ", project.urlImages);
    await storageRef.listAll().then((result) => {
      result.items.map((images) => {
        images.getDownloadURL().then((url) => {
       
            tempImages.push(url);
            addImagesToData(tempImages)
          
          
        });
      });
    });
  };

  const addImagesToData = async (urls) => {
    let imgArray=[]

    urls.map((url)=>{
      console.log("url",url)
      let img ={
        original:url,
        thumbnail:url
      }
     if (imgArray.length<=4) {
      imgArray.push(img)
      

     }
     setImages(imgArray)
     console.log(Images)
    })

  };

  return (
    <div>
      <div
        className="border-r f border-b border-l mx-auto lg:w-4/5 border-gray-400 lg:border-l-0 lg:border-t 
          lg:border-gray-400 bg-white rounded-b lg:rounded-b-none lg:rounded-r p-8 md:p-2
          px-12 flex lg:flex-row flex-col-reverse md:flex-row justify-between leading-normal my-6"
      >
        <div className="mb-8 flex flex-col justify-center lg:w-5/6 w-5/12 pl-5 lg:mt-0 mt-8">
          <h1 className="text-black font-semibold text-2xl w-64">
            {project.name}
          </h1>
          <p className="w-64 lg:mt-6">{project.description}</p>

          <a
            href={project.urlRepository}
            target="_blank"
            rel="noopener noreferrer"
            className="text-blue-500 border-b  border-blue-500 w-32 mt-6"
          >
            Conoce mas
          </a>
        </div>

        <div className="flex justify-end mb-12 text-lg lg:w-2/3 w-64 font-sans mr-12 font-medium text-black flex-col">
          <p>
            {" "}
            <span className="font-semibold">Cliente: </span>
            {project.clientName}
          </p>
          <p>
            {" "}
            <span className="font-semibold">Empresa D: </span> {project.company}
          </p>
          <p>
            {" "}
            <span className="font-semibold">Especialidad: </span>
            {project.specialty}
          </p>
        </div>

        <div className="lg:w-6/12 lg:mb-0 mb-8">
          <GalleryWorks images={Images} />
        </div>
      </div>
    </div>
  );
}
